import React, { useState } from "react";
import classNames from "classnames";
import { IFavoriteButton } from "./types";

const FavoriteButton: React.FC<IFavoriteButton> =({
    title,
    color,
}) => {

    const [isFavorite, setIsFavorite] = useState(false);

    const ButtonClass = classNames({
        'text-white font-bold text-center p-2 border-2 border-blue-300 rounded-lg w-fit h-fit': true,
        'bg-blue-700 hover:bg-blue-900': color === 'blue',
        'bg-rose-700 hover:bg-rose-900': color === 'red',
    })
    
    const handleOnClick = () => {
        setIsFavorite(!isFavorite);
    }

    const titleValues = isFavorite ? '☹︎ Remove from favorites' : '♥︎ Add to favorites';
    const colorValues = isFavorite ? 'blue' : 'red';

    return (
        <button onClick={handleOnClick} className={ButtonClass} color={colorValues}> {titleValues} </button>
    )
}

export default FavoriteButton;